import "./asset/css/reset.css";
import "./asset/css/main.scss";
import * as PIXI from 'pixi.js';
import $ from "jquery";
import {
    scaleToWindow
} from "./asset/js/scaleToWindow.js";

let type = "WebGL";
if (!PIXI.utils.isWebGLSupported()) {
    type = "canvas";
}
PIXI.utils.sayHello(type);
// import Icon from './asset/img/bg.png';  

let Application = PIXI.Application,
    Sprite = PIXI.Sprite;

let loader = new PIXI.Loader(),
    resources = loader.resources;


// PIXI
let app = new Application({
    width: 100,
    height: 100,
    antialias: true,
    transparent: false,
    resolution: 1
});

app.renderer.view.style.position = "absolute";
app.renderer.view.style.display = "block";
app.renderer.autoResize = true;
// // app.renderer.resize(window.innerWidth, window.innerHeight);
$('#scene').append(app.view);
scaleToWindow(app.view, '#2C3539');

loader
    .add({
        url: "./asset/img/figure.png"
    })
    .add("./asset/img/figure1.png")
    .add("./asset/img/loading.png")
    .add("./asset/img/loading_ball.png")
    .add("./asset/file/main.json")
    .load(setup);

// 
// /
loader.onProgress.add((e, resource) => {
    console.info('progress', e.progress);
    ! function (e) {
        var n = parseInt(e % 10),
            t = Math.floor(e / 10);
        $(".loading_num .num").eq(1).removeClass().addClass("num").addClass("n" + t), $(".loading_num .num").eq(2).removeClass().addClass("num").addClass("n" + n), 100 === e && ($(".loading_num .num").eq(0).css({
            display: "inline-block"
        }), $(".loading_num .num").eq(1).removeClass().addClass("num").addClass("n0"), $(".loading_num .num").eq(2).removeClass().addClass("num").addClass("n0"))
    }(parseInt(e.progress))

});
// 
loader.onError.add(() => {
    console.info('error');
});
// 
loader.onLoad.add(() => {
    console.info('onload');
});
// 
loader.onComplete.add(() => {
    console.info('complete');
});

function loadProgressHandler() {
    console.info("loading");
}

//setup
function setup() {
    console.info('setup');
    //
    let figure = new Sprite(resources["./asset/img/figure.png"].texture);
    let loadingBall = new Sprite(resources["./asset/img/loading_ball.png"].texture);
    //
    app.stage.addChild(figure);
    app.stage.addChild(loadingBall);
}